import React from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { formatCurrency } from '@/lib/utils';

interface DebtSummaryProps {
  totalDebt: number;
  totalPeople: number;
  hiddenDebt: number;
  language: 'en' | 'pt';
}

const translations = {
  en: {
    totalToPay: 'Total to Pay',
    totalPeople: 'Total People',
    hiddenDebts: 'Hidden Debts'
  },
  pt: {
    totalToPay: 'Total a Pagar',
    totalPeople: 'Total de Pessoas',
    hiddenDebts: 'Dívidas Ocultas'
  }
};

export function DebtSummary({ totalDebt, totalPeople, hiddenDebt, language }: DebtSummaryProps) {
  const t = translations[language];
  
  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
      <Card className="bg-gradient-to-br from-blue-50 to-indigo-100 dark:from-blue-900 dark:to-indigo-900 border-blue-200 dark:border-blue-700">
        <CardHeader className="pb-2">
          <CardTitle className="text-sm font-medium text-blue-700 dark:text-blue-300">
            {t.totalToPay}
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-2xl font-bold text-blue-900 dark:text-blue-100">
            {formatCurrency(totalDebt)}
          </div>
        </CardContent>
      </Card>
      
      <Card className="bg-gradient-to-br from-green-50 to-emerald-100 dark:from-green-900 dark:to-emerald-900 border-green-200 dark:border-green-700">
        <CardHeader className="pb-2">
          <CardTitle className="text-sm font-medium text-green-700 dark:text-green-300">
            {t.totalPeople}
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-2xl font-bold text-green-900 dark:text-green-100">
            {totalPeople}
          </div>
        </CardContent>
      </Card>
      
      <Card className="bg-gradient-to-br from-orange-50 to-amber-100 dark:from-orange-900 dark:to-amber-900 border-orange-200 dark:border-orange-700">
        <CardHeader className="pb-2">
          <CardTitle className="text-sm font-medium text-orange-700 dark:text-orange-300">
            {t.hiddenDebts}
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-2xl font-bold text-orange-900 dark:text-orange-100">
            {formatCurrency(hiddenDebt)}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}